.main {
    width: 500px;
    height: auto;
    background-color: #2A4747;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.inputBox {
    margin: 30px 0;
    width: 420px;
    height: 60px;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.text {
    margin: 0 30px;
    font-size: 40px;
}
.buttonSection {
    width: 420px;
    margin: 0 0 30px 0;
    display: flex;
    justify-content: space-between;
}
.arithmeticButton {
    width: 100px;
    margin-right: 8px;
    display: flex;
    flex-direction: column;
}
.button {
    width: 100px;
    height: 100px;
    font-size: 40px;
    background-color: white;
    border: 0;
    border-radius: 5px;
    margin: 3px 0;
    cursor: pointer;
}
.margin-right3px {
    margin-right: 6px;
}
.buttonZero {
    width: 206px !important;
}
.numButton {
    display: flex;
    flex-direction: column;
    width: 312px;
}
.numRow {
    display: flex;
    width: 100%;
}